<template>
  <div>
    <b-button class="w-100" id="show-btn" @click="showModal"></b-button>

    <b-modal ref="my-modal" hide-footer title="">
      <div class="d-block">
        <p>
             3 . <br> 
                ! <br> 
                 . <br> 
                    ,        . <br> 
          3400           40 . <br> 
                   . <br> 
                   . <br> 
                     . 
        </p>
      </div>
      <b-button class="mt-3" variant="outline-danger" block @click="hideModal"> </b-button>
    </b-modal>
  </div>
</template>

<script>
  export default {
    methods: {
      showModal() {
        this.$refs['my-modal'].show()
      },
      hideModal() {
        this.$refs['my-modal'].hide()
      }
    }
  }
</script>


